// app/page.tsx
'use client';
import { useState, useEffect } from 'react';
import PresidentialDashboard from '../components/PresidentialDashboard';
import SecretIntelligence from '../components/SecretIntelligence';
import EconomicWarfare from '../components/EconomicWarfare';

interface Country {
  id: string;
  name: string;
  gdp: number;
  gdp_growth_rate: number;
  military_strength: number;
  nuclear_arsenal: number;
  technology_level: number;
  resources: {
    oil: number;
    uranium: number;
    food: number;
  };
}

// Update the TabType
type TabType = 'dashboard' | 'economic' | 'military' | 'diplomacy' | 'internal' | 'crisis' | 'intelligence';


export default function PresidentialOffice() {
  const [activeTab, setActiveTab] = useState<TabType>('dashboard');
  const [countries, setCountries] = useState<Country[]>([]);
  const [szabanData, setSzabanData] = useState<Country | null>(null);

  useEffect(() => {
    // eslint-disable-next-line react-hooks/immutability
    fetchGameState();
    const interval = setInterval(fetchGameState, 10000); // Update every 10 seconds
    return () => clearInterval(interval);
  }, []);

  const fetchGameState = async () => {
    try {
      const response = await fetch('http://localhost:8000/game-state');
      const data = await response.json();
      setCountries(data.countries);
      const szaban = data.countries.find((c: Country) => c.name === 'SZABAN');
      setSzabanData(szaban);
    } catch (error) {
      console.error('Error fetching game state:', error);
    }
  };

  return (
    <div className="presidential-office">
      {/* Presidential Header */}
      <header className="presidential-header">
        <div className="header-content">
          <h1>üá∏üáø Office of the President - Republic of SZABAN</h1>
          <div className="president-status">
            {szabanData && (
              <div className="status-cards">
                <div className="status-card">
                  <span className="label">GDP Rank:</span>
                  <span className="value">
                    #{countries.sort((a, b) => b.gdp - a.gdp).findIndex(c => c.name === 'SZABAN') + 1}
                  </span>
                </div>
                <div className="status-card">
                  <span className="label">Military Power:</span>
                  <span className="value">{szabanData.military_strength}</span>
                </div>
                <div className="status-card">
                  <span className="label">Nuclear Arsenal:</span>
                  <span className="value">{szabanData.nuclear_arsenal}</span>
                </div>
              </div>
            )}
          </div>
        </div>
      </header>

      {/* Navigation Tabs */}
  
<nav className="presidential-tabs">
  <button className={`tab ${activeTab === 'dashboard' ? 'active' : ''}`} onClick={() => setActiveTab('dashboard')}>
    üèõÔ∏è Dashboard
  </button>
  <button className={`tab ${activeTab === 'economic' ? 'active' : ''}`} onClick={() => setActiveTab('economic')}>
    üí∞ Economic
  </button>
  <button className={`tab ${activeTab === 'military' ? 'active' : ''}`} onClick={() => setActiveTab('military')}>
    ‚öîÔ∏è Military
  </button>
  <button className={`tab ${activeTab === 'diplomacy' ? 'active' : ''}`} onClick={() => setActiveTab('diplomacy')}>
    ü§ù Diplomacy
  </button>
  <button className={`tab ${activeTab === 'internal' ? 'active' : ''}`} onClick={() => setActiveTab('internal')}>
    üèõÔ∏è Internal
  </button>
  <button className={`tab ${activeTab === 'crisis' ? 'active' : ''}`} onClick={() => setActiveTab('crisis')}>
    üö® Crisis
  </button>
  <button className={`tab ${activeTab === 'intelligence' ? 'active' : ''}`} onClick={() => setActiveTab('intelligence')}>
    üïµÔ∏è Intelligence
  </button>
</nav>

      {/* Main Content */}
      <main className="presidential-main">
  {activeTab === 'dashboard' && <PresidentialDashboard />}
  {activeTab === 'economic' && <EconomicWarfare countries={countries} />}
  {activeTab === 'military' && <MilitaryStrategy countries={countries} />}
  {activeTab === 'diplomacy' && <DiplomaticRelations countries={countries} />}
  {activeTab === 'internal' && <InternalAffairs countries={countries} />}
  {activeTab === 'crisis' && <CrisisManagement />}
  {activeTab === 'intelligence' && <EnhancedIntelligence countries={countries} />}
</main>

      {/* Presidential Footer */}
<footer className="presidential-footer">
  <p>üîí Classified - For Presidential Eyes Only</p>
  <p>Secure Terminal Active | Last Updated: <span suppressHydrationWarning>{new Date().toLocaleTimeString()}</span></p>
</footer>
    </div>
  );
}