// components/EconomicWarfare.tsx
'use client';
import { useState } from 'react';

interface Country {
  id: string;
  name: string;
  gdp: number;
  gdp_growth_rate: number;
  military_strength: number;
  resources: {
    oil: number;
    uranium: number;
    food: number;
  };
}

interface EconomicWarfareProps {
  countries: Country[];
}

interface Sanction {
  type: string;
  target: string;
  strength: number;
  duration: number;
}

export default function EconomicWarfare({ countries }: EconomicWarfareProps) {
  const [selectedCountry, setSelectedCountry] = useState<string>('');
  const [sanctionType, setSanctionType] = useState<string>('trade');
  const [sanctionStrength, setSanctionStrength] = useState<number>(50);
  const [activeSanctions, setActiveSanctions] = useState<Sanction[]>([]);
  const [espionageTarget, setEspionageTarget] = useState<string>('');
  const [espionageResults, setEspionageResults] = useState<string>('');

  const targetCountries = countries.filter(country => country.name !== 'SZABAN');

  const imposeSanctions = async () => {
    if (!selectedCountry) return;

    const sanction: Sanction = {
      type: sanctionType,
      target: selectedCountry,
      strength: sanctionStrength,
      duration: 12 // months
    };

    try {
      const response = await fetch('http://localhost:8000/economic/sanctions', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          target_country: selectedCountry,
          sanction_type: sanctionType,
          strength: sanctionStrength
        })
      });

      if (response.ok) {
        setActiveSanctions(prev => [...prev, sanction]);
        alert(`Sanctions imposed on ${selectedCountry}`);
      }
    } catch (error) {
      console.error('Error imposing sanctions:', error);
    }
  };

  const conductEconomicEspionage = async () => {
    if (!espionageTarget) return;

    try {
      const response = await fetch('http://localhost:8000/economic/espionage', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          target_country: espionageTarget
        })
      });

      const data = await response.json();
      setEspionageResults(data.report);
    } catch (error) {
      console.error('Error conducting espionage:', error);
    }
  };

  const liftSanctions = (target: string) => {
    setActiveSanctions(prev => prev.filter(s => s.target !== target));
    // API call to lift sanctions would go here
  };

  return (
    <div className="economic-warfare">
      <div className="warfare-header">
        <h2>üí∞ Economic Warfare Command</h2>
        <p>Strategic economic operations to maintain SZABANs dominance</p>
      </div>

      <div className="warfare-grid">
        {/* Sanctions Panel */}
        <div className="action-panel sanctions-panel">
          <h3>‚ö° Economic Sanctions</h3>
          
          <div className="form-group">
            <label>Target Country:</label>
            <select 
              value={selectedCountry} 
              onChange={(e) => setSelectedCountry(e.target.value)}
            >
              <option value="">Select target country</option>
              {targetCountries.map(country => (
                <option key={country.id} value={country.name}>
                  {country.name} (GDP: ${(country.gdp / 1e9).toFixed(1)}B)
                </option>
              ))}
            </select>
          </div>

          <div className="form-group">
            <label>Sanction Type:</label>
            <select 
              value={sanctionType} 
              onChange={(e) => setSanctionType(e.target.value)}
            >
              <option value="trade">Trade Embargo</option>
              <option value="financial">Financial Sanctions</option>
              <option value="technology">Technology Export Ban</option>
              <option value="energy">Energy Sanctions</option>
            </select>
          </div>

          <div className="form-group">
            <label>Sanction Strength: {sanctionStrength}%</label>
            <input 
              type="range" 
              min="10" 
              max="100" 
              value={sanctionStrength}
              onChange={(e) => setSanctionStrength(Number(e.target.value))}
            />
          </div>

          <button 
            className="btn-sanctions"
            onClick={imposeSanctions}
            disabled={!selectedCountry}
          >
            üö´ Impose Sanctions
          </button>
        </div>

        {/* Espionage Panel */}
        <div className="action-panel espionage-panel">
          <h3>üïµÔ∏è Economic Espionage</h3>
          
          <div className="form-group">
            <label>Espionage Target:</label>
            <select 
              value={espionageTarget} 
              onChange={(e) => setEspionageTarget(e.target.value)}
            >
              <option value="">Select target for intelligence</option>
              {targetCountries.map(country => (
                <option key={country.id} value={country.name}>
                  {country.name}
                </option>
              ))}
            </select>
          </div>

          <button 
            className="btn-espionage"
            onClick={conductEconomicEspionage}
            disabled={!espionageTarget}
          >
            üîç Conduct Economic Espionage
          </button>

          {espionageResults && (
            <div className="espionage-results">
              <h4>Espionage Report:</h4>
              <p>{espionageResults}</p>
            </div>
          )}
        </div>

        {/* Active Sanctions */}
        <div className="action-panel active-sanctions">
          <h3>üìã Active Sanctions</h3>
          {activeSanctions.length === 0 ? (
            <p>No active sanctions</p>
          ) : (
            activeSanctions.map((sanction, index) => (
              <div key={index} className="sanction-item">
                <div className="sanction-info">
                  <strong>{sanction.target}</strong>
                  <span>Type: {sanction.type}</span>
                  <span>Strength: {sanction.strength}%</span>
                </div>
                <button 
                  className="btn-lift"
                  onClick={() => liftSanctions(sanction.target)}
                >
                  Lift
                </button>
              </div>
            ))
          )}
        </div>

        {/* Economic Intelligence */}
        <div className="action-panel economic-intel">
          <h3>üìä Economic Intelligence</h3>
          <div className="intel-grid">
            {targetCountries.map(country => (
              <div key={country.id} className="country-intel">
                <h4>{country.name}</h4>
                <p>GDP: ${(country.gdp / 1e9).toFixed(1)}B</p>
                <p>Growth: {country.gdp_growth_rate.toFixed(1)}%</p>
                <p>Resources: 
                  Oil: {country.resources.oil} | 
                  Food: {country.resources.food}
                </p>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}