// components/PresidentialDashboard.tsx
'use client';
import { useState, useEffect } from 'react';

interface Country {
  id: string;
  name: string;
  gdp: number;
  military_strength: number;
  nuclear_arsenal: number;
}

export default function PresidentialDashboard() {
  const [countries, setCountries] = useState<Country[]>([]);
  const [selectedAction, setSelectedAction] = useState('');
  const [targetCountry, setTargetCountry] = useState('');

  const fetchGameState = async () => {
  try {
    const response = await fetch('http://localhost:8000/game-state');
    if (!response.ok) throw new Error('Failed to fetch');
    const data = await response.json();
    setCountries(data.countries || []);
  } catch (error) {
    console.error('Error fetching game state:', error);
    setCountries([]);
  }
};

  useEffect(() => {
    // eslint-disable-next-line react-hooks/set-state-in-effect
    fetchGameState();
  }, []);

  const executePresidentialAction = async (action: string) => {
    await fetch('http://localhost:8000/president/action', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        action_type: action,
        target_country: targetCountry,
        details: {}
      })
    });
    fetchGameState();
  };

  return (
    <div className="presidential-dashboard">
      <h1>Office of the President - SZABAN</h1>
      
      <div className="country-rankings">
        <h2>Global Power Rankings</h2>
        <table>
          <thead>
            <tr>
              <th>Rank</th>
              <th>Country</th>
              <th>GDP ($)</th>
              <th>Military Power</th>
              <th>Nuclear Arsenal</th>
            </tr>
          </thead>
          <tbody>
            {countries.sort((a, b) => b.gdp - a.gdp).map((country, index) => (
              <tr key={country.id} className={country.name === 'SZABAN' ? 'our-country' : ''}>
                <td>{index + 1}</td>
                <td>{country.name}</td>
                <td>${country.gdp.toLocaleString()}</td>
                <td>{country.military_strength}</td>
                <td>{country.nuclear_arsenal}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      <div className="presidential-actions">
        <h2>Presidential Actions</h2>
        <select onChange={(e) => setTargetCountry(e.target.value)}>
          <option value="">Select Target Country</option>
          {countries.filter(c => c.name !== 'SZABAN').map(country => (
            <option key={country.id} value={country.name}>{country.name}</option>
          ))}
        </select>
        
        <div className="action-buttons">
          <button onClick={() => executePresidentialAction('trade_sanctions')}>
            Impose Trade Sanctions
          </button>
          <button onClick={() => executePresidentialAction('military_exercises')}>
            Conduct Military Exercises
          </button>
          <button onClick={() => executePresidentialAction('diplomacy')}>
            Initiate Diplomacy
          </button>
          <button onClick={() => executePresidentialAction('intelligence_gathering')}>
            Gather Intelligence
          </button>
        </div>
      </div>
    </div>
  );
}